* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #d6d6fc;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .15));
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #6a6ab7;
  border-radius: 2px;
  outline: rgba(0, 0, 0, 0);
  margin-bottom: 16px;
  padding: 8px;
  transition-property: background-color, border-color, outline-color, outline-width;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

#search-box:hover, #search-box:focus {
  background-color: #c4c4fe;
  border-color: rgba(0, 0, 0, 0);
  outline: 2px solid #8d8dc9;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.country-item {
  align-items: center;
  gap: 10px;
  font-size: 18px;
  font-weight: 500;
  display: flex;
}

.country-item:not(:last-child) {
  margin-bottom: 5px;
}

.country-name {
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
  font-size: 30px;
  display: flex;
}

.description-item {
  align-items: center;
  column-gap: 8px;
  display: flex;
}

.description-item:not(:last-child) {
  margin-bottom: 5px;
}

.description-title {
  font-size: 18px;
}

@media screen and (max-width: 1200px) {
  .country-name {
    font-size: 20px;
  }

  .description-item:last-child {
    align-items: start;
  }

  .description-title {
    font-size: 16px;
  }
}

